import Delete from './Delete';
import styles from './pages/RegisPage.module.css';

function Images({ name, images, setValues }) {

	const handleDeleteImage = (e, image) => {
		setValues(draft => ({ ...draft, images: images.filter(img => img !== image) }));
	};

	return (
		<ul>
			{images.map(image => {
				return (
					<li key={image} className={styles[".img-container"]}><img src={image} alt={name}/><button className={styles.delete} onClick={(e) => handleDeleteImage(e, image)}><Delete/></button></li>
				);
			})}
		</ul>
	);
}

export default Images;
